import { SOCKET_INITIALIZED, SOCKET_CLOSED } from '../actions/actionTypes';

const initialState = {
    Socket: null
}

export default (state = initialState, action = {}) => {
    switch(action.type) {
        case SOCKET_INITIALIZED:
        return {
            Socket: action.value
        }
        case SOCKET_CLOSED:
        return {
            Socket: null
        }
        default: return state;
    }
}