import { CHANGE_HEADER_NAVBAR_SHOW, CHANGE_FOOTER_NAVBAR_SHOW } from '../actions/actionTypes';

const initialState = {
    showHeaderNavbar: true,
    showFooterNavbar: true,
}

export default (state = initialState, action = {}) => {
    switch (action.type) {
        case CHANGE_HEADER_NAVBAR_SHOW:
            return {
                showHeaderNavbar: action.value,
                showFooterNavbar: state.showFooterNavbar,
            }
        case CHANGE_FOOTER_NAVBAR_SHOW:
            return {
                showFooterNavbar: action.value,
                showHeaderNavbar: state.showHeaderNavbar,
            }
        default: return state;
    }
}