import { SET_LOGIN_LOCAL_STORAGE, REMOVE_STORAGE, USER, AJAX_PUBLIC_REQUEST } from '../../Constants/AppConstants';
import history from '../../history';
import { SET_CURRENT_USER } from './actionTypes';

export function setCurrentUser(user) {
    return {
        type: SET_CURRENT_USER,
        user
    }
}

export function logout() {
    return dispatch => {
        REMOVE_STORAGE(USER);
        REMOVE_STORAGE("cart");
        dispatch(setCurrentUser({}));
    }
}

export function serviceLogout() {
    return dispatch => {
        REMOVE_STORAGE(USER);
        dispatch(setCurrentUser({}));
    }
}

export function userLoginRequest(userData) {
    return dispatch => {
        const request_result = AJAX_PUBLIC_REQUEST("POST", "user/login", userData);
        request_result.then(results => {
            if (parseInt(results.response.code) === 1000) {
                let user_data = results.response.data;
                user_data.remember = false;
                if (userData.remember === true) {
                    user_data.remember = true;
                }
                SET_LOGIN_LOCAL_STORAGE(JSON.stringify(user_data));
                dispatch(setCurrentUser(user_data));
            } else {
                dispatch(setCurrentUser({}));
                // location.reload();
            }
        }
        );
        return request_result;
    }
}