// Import React and ReactDOM
import React, { Suspense } from 'react';
import ReactDOM from 'react-dom';

import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import { Router } from 'react-router-dom';
import { createStore, applyMiddleware, compose } from 'redux';
import * as serviceWorker from './serviceWorker';
import history from './../history';
import reducer from './../Store/reducers/reducer';
// Import Framework7
import Framework7 from 'framework7/framework7-lite.esm.bundle.js';

// Import Framework7-React Plugin
import Framework7React from 'framework7-react';

// Import Framework7 Styles
import 'framework7/css/framework7.bundle.css';

import { USER, GET_STORAGE, SET_LOGIN_LOCAL_STORAGE, AJAX_REQUEST, REMOVE_STORAGE } from './../Constants/AppConstants';
import { setCurrentUser } from './../Store/actions/loginActions';
import {RandomLoader} from './../pages/Common/ReactContentLoader';

// Import Icons and App Custom Styles
import '../css/icons.css';
import '../css/Auth.css';
import '../css/app.css';
import '../css/elias.css';
import '../css/BikeHome.css';
import '../css/OilHome.css';
import '../css/Loyalty.css';
import '../css/UserProfile.css';
import '../css/Notificaton.css';
import '../css/Insurance.css';
import '../css/Card.css';
import '../css/reset.css';

// Import App Component
// import App from '../components/app.jsx';
const App = React.lazy(() => import('../components/app.jsx'));


// setInterval(function(){
//   if (window.location.href.indexOf("error") > -1) {

//   }
//   else {

//   }
// }, 3000);

const store = createStore(
  reducer,
  compose(
      applyMiddleware(thunk),
      window.__REDUX_DEVTOOLS_EXTENSION__ ? window.__REDUX_DEVTOOLS_EXTENSION__() : f => f
  )
);

const user_cookie = GET_STORAGE(USER);
if (user_cookie) {
    AJAX_REQUEST('POST','user/getCustomerProfile',{
      customer_id: JSON.parse(user_cookie)['id']
    }).then(results => {
        if (parseInt(results.response.code) === 1000) {
            store.dispatch(setCurrentUser(JSON.parse(user_cookie)));
            SET_LOGIN_LOCAL_STORAGE(user_cookie);
        } else {
            store.dispatch(setCurrentUser({}));
            REMOVE_STORAGE(USER);
        }
      });
    store.dispatch(setCurrentUser(JSON.parse(user_cookie)));
    SET_LOGIN_LOCAL_STORAGE(user_cookie);
}
else {
  store.dispatch(setCurrentUser({}));
  REMOVE_STORAGE(USER);
  // history.push('/#/home/');
  // setTimeout(function(){
  //   location.reload(); 
  // },100);
}

// Init F7 Vue Plugin
Framework7.use(Framework7React)


// Mount React App
// ReactDOM.render(
//   React.createElement(App),
//   document.getElementById('app'),
// );

ReactDOM.render(
  <Provider store={store}>
      <Router history={history}>
          {/* <App /> */}
          <Suspense fallback={<RandomLoader/>}>
            <App />
          </Suspense>
      </Router>
  </Provider>,
  document.getElementById('app')
);

serviceWorker.unregister();