export function AJAX_SOCKET_CLIENT_REQUEST(type = 'GET', additional_url, data) {
    const c_user_user = GET_COOKIE(USER);
    let c_user = false;
    if (c_user_user) {
        c_user = JSON.parse(c_user_user);
    } else {
        history.push('/');
    }

    let rdata = '';

    if (c_user) {
        let promise = $.Deferred();
        $.ajax({
            type: type,
            url: SOCKET_API_URL + additional_url,
            data: data,
            timeout: 60000,
            headers: {
                'Authorization': `Bearer ${c_user.client_token}`,
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            success: function (resp) {
                if (resp) {
                    promise.resolve(resp);
                } else {
                    const respo = {
                        "status": "error",
                        "error": { "message": "Something went wrong. Please refresh the page again." },
                    };
                    promise.resolve(respo);
                }
            },
            error: function (resp, textStatus) {
                if (textStatus === 'timeout') {
                    const respo = {
                        "status": "error",
                        "error": { "message": "Connection timeout. Please try again." },
                    };
                    promise.resolve(respo);
                } else {
                    if (resp.status === 500) {
                        const respo = {
                            "status": "error",
                            "error": { "message": "500. Internal Server Error." },
                        };
                        promise.resolve(respo);
                    } else {
                        const respo = {
                            "status": "error",
                            "error": { "message": "Something went wrong. Please try again." },
                        };
                        promise.resolve(respo);

                        // SET_STORAGE('settings', JSON.stringify(SITEDOWN_DATA));
                        // history.push("/error");
                        SET_STORAGE('settings', JSON.stringify(SITEDOWN_DATA));
                        alert("Something went wrong!")
                    }
                }
            }
        });
        rdata = promise.promise();
    }
    return rdata;
}

