// EMOJI RENDER AND PARSER GLOBAL

export function ParseEmojisNoLine(value) {
    const emojisArray = toArray(value);
        const newValue = emojisArray.reduce((previous, current) => {
            if (typeof current === "string") {
                return previous + current;
            }
            return previous + current.props.children;
        }, "");
        return newValue;
}
    

export function ParseEmojis(value) {
    if((/\s/.test(value)) || (value.indexOf(': :') > -1)){
        const emojisArray = toArray(value.replace(/\n/g, "<br />"));
        const newValue = emojisArray.reduce((previous, current) => {
            if (typeof current === "string") {
                return previous + current;
            }
            return previous + current.props.children;
        }, "");
        return newValue;
    }
    else if((/\s/.test(value)) || (value.indexOf('::') > -1) && value.startsWith(':') && value.endsWith(':')){
        const emojisArray = toArray(value.replace(/\n/g, "<br />"));
        const newValue = emojisArray.reduce((previous, current) => {
            if (typeof current === "string") {
                return previous + current;
            }
            return previous+`<span class='make-emojis-medium'>${current.props.children}</span>`;
        }, "");
        return newValue;
    }
    else if (value.startsWith(':') && value.endsWith(':')) {
        const emojisArray = toArray(value.replace(/\n/g, "<br />"));
        const newValue = emojisArray.reduce((previous, current) => {
            if (typeof current === "string") {
                return previous + current;
            }
            return previous+`<span class='make-emojis-large'>${current.props.children}</span>`;
            
        }, "");
        return newValue;
    }
    else {
        const emojisArray = toArray(value.replace(/\n/g, "<br />"));
        const newValue = emojisArray.reduce((previous, current) => {
            if (typeof current === "string") {
                return previous + current;
            }
            // return previous+`<span class='make-emojis-large'>${current.props.children}</span>`;
            return previous + current.props.children;
            
        }, "");
        return newValue;
    }
}


export function ParseEmojisAndHtml(value) {
    const emojisArray = toArray(value);
    const newValue = emojisArray.map(node => {
        if (typeof node === "string") {
            return <span dangerouslySetInnerHTML={{ __html: node }} />;
        }
        return node.props.children;
    });
    return newValue;
}


export function ParseEmojisToHtml(value) {
    const emojisArray = toArray(value);
    const newValue = emojisArray.reduce((previous, current) => {
        if (typeof current === "string") {
            return previous + current;
        }
        return previous + getEmojiDataFromNative(current.props.children, '', data)['colons'];
    }, "");
    return newValue;
}


export function ADD_TO_CART(product, type, item_on_cart) {

    // console.log('type',type);
    // console.log('item_on_cart',item_on_cart);

    let cart = [];
    let data = [];

    if (!GET_STORAGE("cart")) {
        SET_STORAGE("cart", JSON.stringify(cart));
    }

    cart = JSON.parse(GET_STORAGE("cart"));

    let newItem = {
        id: product.id,
        title: product.title,
        price: product.price,
        discounted_price: product.discounted_price,
        photo: product.photo[0],
        status: product.status,
        quantity: 1,
    }

    if (cart.length > 0) {
        cart.forEach(function (item, key) {
            if (item.id == product.id) {
                // if (parseInt(item.quantity) >= parseInt(e.target.getAttribute('cart_quantity'))) {
                //     alert("Out Of Stock") // Check product quantity
                // } else {
                    // item.quantity = Number(Number(item.quantity) + 1);
                // }

                if(type == 'add'){
                    item.quantity = Number(Number(item.quantity) + 1);
                    data.push(item); 
                    newItem = null;
                    // console.log('add',item_on_cart);
                }
                else if(type == 'substruct'){
                    if(item_on_cart > 0){
                        item.quantity = Number(Number(item.quantity) - 1);
                        data.push(item);
                    }
                    newItem = null;
                    // console.log('substruct',item_on_cart);
                }
                else if(type == 'delete'){
                    newItem = null;
                    // console.log('delete',item_on_cart);
                }else{
                    newItem = null;
                    // console.log('else',item_on_cart);
                }
            } else {
                // console.log('new',item_on_cart);
                data.push(item);
            }
        });
        if (newItem != null) {
            // console.log('new not null',item_on_cart);
            data.push(newItem);
        }
    } else {
        if(type != 'delete'){
            // console.log('totally new',item_on_cart);
            data.push(newItem);
        }
    }

    SET_STORAGE("cart", JSON.stringify(data));
    //this.props.updateCartItems(); // Update cart item_count method
}